
"use client";

import { useState, useEffect } from "react";
import { PageHeader } from "@/components/page-header";
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { fuelService, FuelData } from "@/lib/services/fuelService";
import { Loader2, Fuel as FuelIcon, Newspaper } from "lucide-react";
import { cn } from "@/lib/utils";
import { format } from "date-fns";

export default function FuelPricesPage() {
  const [fuelData, setFuelData] = useState<FuelData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isPaused, setIsPaused] = useState(false);

  useEffect(() => {
    const unsubscribe = fuelService.onLatestFuelData(
      (data) => {
        setFuelData(data);
        setLoading(false);
        setError(null);
      },
      (err) => {
        console.error("Failed to subscribe to fuel data:", err);
        setError("Could not load live fuel price data. Please check the connection to the data source.");
        setLoading(false);
      }
    );

    return () => unsubscribe();
  }, []);

  const priceColor = (p: number | undefined | null) => {
    if (p === null || p === undefined) return "text-muted-foreground";
    if (p >= 2.3) return "text-destructive";
    if (p >= 1.6) return "text-amber-600";
    return "text-green-600";
  };
  
  const fuelPrices = fuelData ? [
    { label: "Petrol 91", value: fuelData.petrol91 },
    { label: "Petrol 95", value: fuelData.petrol95 },
    { label: "Diesel", value: fuelData.diesel },
    { label: "Kerosene", value: fuelData.kerosene },
    { label: "LPG", value: fuelData.lpg },
  ] : [];

  const marqueeItems = fuelData ? [
    ...fuelPrices.map((fuel, index) => (
        <div key={`fuel-${index}`} className="flex items-center space-x-2 mx-6 py-4">
            <FuelIcon className={cn("h-5 w-5", priceColor(fuel.value))} />
            <span className="font-semibold">{fuel.label}:</span>
            <span className={cn("font-mono font-bold", priceColor(fuel.value))}>
                SAR {fuel.value?.toFixed(2) ?? "N/A"}
            </span>
        </div>
    )),
    <div key="news" className="flex items-center space-x-2 mx-6 py-4 text-muted-foreground">
        <Newspaper className="h-5 w-5" />
        <span className="font-semibold">Latest News:</span>
        <span>{fuelData.news || "No recent headlines."}</span>
    </div>
  ] : [];

  return (
    <>
      <PageHeader
        title="Official Fuel Prices & News"
        description="Live retail fuel prices scraped from official sources, updated periodically."
      />
      <Card>
        <CardHeader>
          <CardTitle>Live Fuel Price Ticker</CardTitle>
          <CardDescription>
            This data is automatically fetched from official sources and updated every 6 hours.
          </CardDescription>
        </CardHeader>
        <CardContent>
          {loading ? (
            <div className="flex h-24 items-center justify-center text-muted-foreground">
              <Loader2 className="mr-2 h-5 w-5 animate-spin" />
              <span>Loading live fuel data...</span>
            </div>
          ) : error ? (
             <div className="flex h-24 items-center justify-center text-destructive">
                <span>{error}</span>
             </div>
          ) : fuelData ? (
            <div 
                className="marquee border rounded-lg bg-muted/30"
                onMouseEnter={() => setIsPaused(true)}
                onMouseLeave={() => setIsPaused(false)}
            >
                <div className={cn("marquee-content", isPaused && "paused")}>
                    {marqueeItems.map((item, index) => (
                        <div key={`item-first-${index}`}>{item}</div>
                    ))}
                    {marqueeItems.map((item, index) => (
                        <div key={`item-second-${index}`}>{item}</div>
                    ))}
                </div>
            </div>
          ) : null}
        </CardContent>
        <CardFooter>
            <p className="text-xs text-muted-foreground">
                {fuelData?.lastUpdated ? `Last updated: ${format(new Date(fuelData.lastUpdated), 'PPP p')}` : 'Awaiting data...'}
            </p>
        </CardFooter>
      </Card>
    </>
  );
}
